}
.filter-dropdown-menu__select {
    border-radius: 10px !important;
    background-color: $white;
    border: 1px solid #DBDDE0 !important;
    height: 38px;
    width: 100%;
}
.filter-dropdown-menu__select .dropdown-menu {
    position: absolute !important;
    background: $white !important;
    box-shadow: 0px 10px 24px rgba(16, 30, 115, 0.13) !important;
    border-radius: 20px !important;
}
.filter-dropdown-menu__select-label {
