    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.10000000149011612px;
    text-align: left;
}
.filter-dropdown-menu__select .vs__dropdown-toggle {
    border-radius: 10px !important;
    border: 1px solid #DBDDE0 !important;
}
.filter-dropdown-menu__select .vs__actions {
    position: relative;
}
.filter-dropdown-menu__select .vs__clear {
    position: absolute;
    left: 34px;
}
.filter-dropdown-menu__select-wrapper {
    padding-top: 12px;
    padding-bottom: 12px;
}
.filter-dropdown-menu__select-wrapper.options {
    padding-top: 12px;
    padding-bottom: 12px;
}
